/* My bookings page */
import React, { Component } from 'react';
import { Alert, Button, Container, Table } from 'react-bootstrap';
import BookingServiceApi from '../../api/BookingServiceApi';
import LocationServiceApi from '../../api/LocationServiceApi';
import CarServiceApi from '../../api/CarServiceApi';
import UserServiceApi from '../../api/UserServiceApi';
import '../../styles/login.css';

const container = {
    color: "white",
}
class MyBookingPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            bookings: [],
            locations: [],
            destinations: [],
            cars: [],
            errorMessage: ''
        };
        this.handleCancelButton = this.handleCancelButton.bind(this);
        this.getUsersBookings = this.getUsersBookings.bind(this);
        this.checkBookingPast = this.checkBookingPast.bind(this);
    }

    componentDidMount() {
        this.getUsersBookings();
        // obtain all locations
        LocationServiceApi.getAllLocations()
            .then(res => {
                let locationArray = this.state.locations;
                let destinationArray = this.state.destinations;
                res.data.forEach(location => {
                    let locationObject = {
                        id: location._id,
                        address: location.address,
                        name: location.name
                    };
                    let destinationObject = {
                        id: location._id,
                        address: location.address,
                        name: location.name
                    }
                    locationArray.push(locationObject);
                    destinationArray.push(destinationObject);

                    this.setState({ locations: locationArray });
                    this.setState({ destinations: destinationArray });
                });
            }).catch((error) => {
                this.setState({ errorMessage: error.response.data.message });
            })
        // obtain all cars
        CarServiceApi.getAllCars()
            .then(res => {
                this.setState({
                    cars: res.data.cars
                });
            }).catch((error) => {
                this.setState({ errorMessage: error.response.data.message });
            });
    }

    getUsersBookings() {
        const userDetails = UserServiceApi.getLoggedInUserDetails();
        BookingServiceApi.getUserBookings(userDetails.id)
            .then(res => {
                this.setState({
                    // sort bookings by latest
                    bookings: res.data.bookings.reverse()
                });
            }).catch((error) => {
                this.setState({ errorMessage: error.response.data.message });
            });
    }

    checkBookingPast(pickupTime) {
        // check if booking pickup time hjas passed current time
        let currentTime = new Date();
        currentTime.setMinutes(currentTime.getMinutes() - currentTime.getTimezoneOffset());
        return new Date(pickupTime) > currentTime;
    }

    handleCancelButton(booking) {
        booking.status = 'Cancelled';
        BookingServiceApi.modifyBooking(booking)
            .then(() => {
                this.getUsersBookings();
            }).catch((error) => {
                this.setState({ errorMessage: error.response.data.message });
            });
    }

    render() {
        return (

            <Container style={container}>
                {this.state.errorMessage && <Alert variant="danger">
                    <Alert.Heading>Error obtaining bookings!</Alert.Heading>
                    <p>
                        {this.state.errorMessage}
                    </p>
                </Alert>}
                <h2>My Bookings</h2>
                <br />
                <Table bordered responsive >
                    <thead>
                        <tr style={container}>
                            <th>Booking ID</th>
                            <th>Booked Time</th>
                            <th>Pickup Time</th>
                            <th>Return Time</th>
                            <th>Bus</th>
                            <th>Cost</th>
                            <th>Location</th>
                            <th>Address</th>
                            <th>Destination</th>
                            <th>Status</th>

                            <th>Details</th>
                            <th>Cancel</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.state.bookings.map(booking =>
                            <tr style={container}>
                                <td>{booking.id}</td>
                                <td>{booking.bookedtime}</td>
                                <td>{booking.pickuptime}</td>
                                <td>{booking.returntime}</td>
                                <td>
                                    {this.state.cars.map(car =>
                                        <>
                                            {car.id === booking.car &&
                                                <>
                                                    {car.make}
                                                </>
                                            }
                                        </>
                                    )}
                                </td>
                                <td>${booking.cost}</td>
                                <td>
                                    {this.state.locations.map(location =>
                                        <>
                                            {location.id === booking.location &&
                                                <>
                                                    {location.name}
                                                </>
                                            }
                                        </>
                                    )}
                                </td>
                                <td>
                                    {this.state.locations.map(location =>
                                        <>
                                            {location.id === booking.location &&
                                                <>
                                                    {location.address}
                                                </>
                                            }
                                        </>
                                    )}
                                </td>
                                <td>
                                    {this.state.destinations.map(destination =>
                                        <>
                                            {destination.id === booking.destination &&
                                                <>
                                                    {destination.address}
                                                </>
                                            }
                                        </>
                                    )}
                                </td>
                                <td>{booking.status}</td>
                                <td>
                                    <Button href={`/mybookings/${booking.id}`}>View</Button>
                                </td>
                                {(booking.status === "Confirmed" && this.checkBookingPast(booking.pickuptime)) &&
                                    <td>
                                        <Button variant="danger" onClick={() => this.handleCancelButton(booking)}>Cancel</Button>
                                    </td>
                                }
                                {(booking.status === "Cancelled" && this.checkBookingPast(booking.pickuptime)) &&
                                    <td>
                                        <Button variant="danger" disabled>Cancel</Button>
                                    </td>
                                }
                            </tr>
                        )}
                    </tbody>
                </Table>
            </Container>
        )
    }
}

export default MyBookingPage;
